/* https://stand-4u.com/css/fukidashi.html */
/*吹き出し*/
.balloon_l,
.balloon_r {
  margin: 30px 0;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start; /*縦位置を上揃え*/
  /*   align-items: center; */ /*縦位置を真ん中揃え*/
}
.balloon_r {
  justify-content: flex-end;
}
.faceicon img {
  width: 80px; /*任意のサイズ*/
  height: auto;
}
.balloon_r .faceicon {
  margin-left: 25px;
}
.balloon_l .faceicon {
  margin-right: 25px;
}
.balloon_r .faceicon {
  order: 2 !important;
}
.says {
  max-width: 500px; /*最大幅は任意*/
  display: flex;
  flex-wrap: wrap;
  position: relative;
  padding: 12px 14px;
  border-radius: 12px;
  background: #333333;
  box-sizing: border-box;
  margin: 0 !important;
  line-height: 1.5;
  color: #fff;
  align-items: center;
}
.says p {
  margin: 8px 0 0 !important;
}
.says p:first-child {
  margin-top: 0 !important;
}
.says:after {
  content: "";
  position: absolute;
  border: 10px solid transparent;
  /*   margin-top:-3px;  */
}
.balloon_l .says:after {
  left: -26px;
  border-right: 22px solid #333333;
}
.balloon_r .says:after {
  right: -26px;
  border-left: 22px solid #333333;
}

body {
  background-color: mintcream;
}

/* 追加分(form) */
.chatform input[type="text"] {
  width: 300px;
  font-size: 13pt;
}
.chatform input[type="image"] {
  vertical-align: top;
  margin-left: 5px;
  width: 30px; /*任意のサイズ*/
  height: auto;
}
